.content {
  padding: 15px 0 15px 0;
  width: 70px;
  flex-direction: row;
  justify-content: center;
  margin: 0 10px;
  z-index: 10;
}

.firstRow {
  display: flex;
  text-align: center;
}

.firstRow canvas{
  width: 30px;
}

.container {
  display: flex;
  flex-flow: row;
  margin: 0 auto 0 auto;
}

.forecastTitle {
  font-size: 8pt;
  margin-bottom: 5px;
  min-width: 90px;
  text-align: center;
  font-weight: bold;
}

.extraShadow {
  color: #ffffff;
  text-shadow: 0 2px 3px rgba(0,0,0,0.8),
  0 4px 13px rgba(0,0,0,0.2),
  0 8px 10px rgba(0,0,0,0.2),
  0 0 20px rgba(0,0,0,0.8),
  0 0 20px rgba(0,0,0,1);
}

.temp {
  position: relative;
  font-size: 16pt;
}

.time {
  font-size: 0.7em;
  text-align: center;
}
