.container{
}

.image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: opacity .5s ease-in-out, visibility .5s ease-in-out;
  opacity: 1;
}

.thumb {
  filter: blur(15px);
  transition: opacity .5s ease-in-out, visibility .5s ease-in-out;
}

.hidden {
  opacity: 0;
  visibility: hidden;
}
